import axios from 'axios';

const baseUrl = 'https://trade-tracker.onrender.com/api';

const axiosInstance = axios.create({
  baseURL: baseUrl,
});

axiosInstance.interceptors.request.use(
  (config) => {
    const token =
      'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..W04ebuQ1q9ixLkrnlNhlXMzABQKTgAyGxCWaqbSfiYk';
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    console.error(error);
  }
);

export const postDates = async ({ startDate, endDate }) => {
  try {
    const res = await axiosInstance.post(`${baseUrl}/transactions/range`, {
      startDate,
      endDate,
    });
    console.log(res);
    return res;
  } catch (error) {
    console.error('[Do followship failed]: ', error);
  }
};
